<template>
  <div >
    <div v-show="expand" style="background-color: white;" class="p-2">
      <label>Give this file a Name</label>
      <span class="esri-icon esri-icon-close close" @click="expand=false"></span>
      <b-input v-model="title"></b-input>
      <b-form-checkbox v-model="uploadForm">Upload form data</b-form-checkbox><br/>
      <b-form-checkbox v-model="uploadResult">Upload result data</b-form-checkbox><br/>
      <b-button @click="upload">Upload</b-button>
    </div>
    <div v-show="!expand" @click="expand = true" class="esri-widget--button esri-widget esri-interactive resize" role="button"  title="Upload to ArcGIS Online">
      <span aria-hidden="true" role="presentation" class="esri-icon esri-icon-applications"></span>
    </div>
  </div>

</template>
<script>
import Papa from 'papaparse'
import {get} from 'vuex-pathify'

export default {
  computed: {
    jobId: get('jobId'),
    jobState: get('jobStatus')
  },
  data: function() {
    return {
      expand: false,
      title: "",
      uploadForm: false,
      uploadResult: true
    }
  },
  methods: {
    publish: function(response2) {
      this.$store.state.esriModules.esriRequest.then(([esriRequest])=>{
        esriRequest(`https://www.arcgis.com/sharing/rest/content/users/${this.$store.state.portal.user.username}/publish`,{
          method: 'post',
          query: {
            itemID: response2.data.id,
            overwrite: true,
            fileType: 'fileGeodatabase',
            publishParameters: JSON.stringify({name: this.title}),
            f: 'json'
          }
        }).then((response3)=>{
          this.$store.set('jobStatus','success')
        }, (error3)=>{
          console.log(error3);
          this.$store.set('jobStatus', 'failed')
          this.$store.set('messages', error3.details.messages)
        });
      });
    },
    publishCSV: function(responseCSV, fields) {
      var fobj = fields.map((field)=>{
        return {
          name: field,
          type: 'esriFieldTypeString',
          alias: field,
          sqlType: 'sqlTypeNVarchar',
          length: 256,
          nullable: true,
          editable: true,
          domain: null,
          defaultValue: null,
          locationType: 'unknown'
        }
      });
      this.$store.state.esriModules.esriRequest.then(([esriRequest])=>{
        esriRequest(`https://www.arcgis.com/sharing/rest/content/users/${this.$store.state.portal.user.username}/publish`,{
          method: 'post',
          query: {
            itemID: responseCSV.data.id,
            overwrite: true,
            fileType: 'csv',
            publishParameters: JSON.stringify({
              name: `${this.title}_form`,
              type: 'csv',
              layerInfo: {
                fields: fobj
              }
            }),
            f: 'json'
          }
        }).then((response3)=>{
          this.$store.set('jobStatus','success')
        }, (error3)=>{
          console.log(error3);
          this.$store.set('jobStatus', 'failed')
          this.$store.set('messages', error3.details.messages)
          this.$emit('updateStatus', {status: 'failed', messages: error3.details.messages})
        });
      })
    },
    addItem: function(response) {
      this.$store.state.esriModules.esriRequest.then(([esriRequest])=>{
        esriRequest(`${this.$store.state.portal.user.userContentUrl}/addItem`,{
          method: 'post',
          query: {
            dataUrl: response.value.url,
            title: this.title,
            overwrite: true,
            type: 'File Geodatabase',
            f: 'json'
          }
        }).then((response2)=>{
          var self = this
          setTimeout(function() {
            self.publish(response2)
          },1000)
        });
      });
    },
    upload: function() {
      this.expand = false;
      if (this.uploadResult||this.uploadForm) this.$store.set('jobStatus', 'processing')
      if (this.uploadResult) {
        this.$store.state.esriModules.Geoprocessor.then(([Geoprocessor])=>{
          new Geoprocessor(`https://logistics.arcgis.com/arcgis/rest/services/World/VehicleRoutingProblem/GPServer/SolveVehicleRoutingProblem`)
          .getResultData(this.jobId, 'out_route_data',{responseType: 'json'})
          .then((response)=>{
            if (!response.value) {
              this.$emit('updateStatus', {status: 'failed', messages: ['Geoprocessing service did not save data']})
              return
            } else {
              var self = this
              setTimeout(function() {
                self.addItem(response)
              },1000);
            }
          });
        });
      }
      if (this.uploadForm) {
        var rp = this.$store.copy('requiredParam')
        var fields = [];
        var data = [];
        for (var key in rp) {
          for (var key2 in rp[key]) {
            if(data.length < Number(key2)) data.push([])
            for (var key3 in rp[key][key2].attributes) {
              if (rp[key][key2].attributes[key3].display) {
                if (!fields.includes(`${key}.${key3}`)) fields.push(`${key}.${key3}`);
                data[Number(key2)-1].push(rp[key][key2].attributes[key3].value)
              }
            }
            for (var key4 in rp[key][key2].geometry) {
              if (!fields.includes(`${key}.${key4}`)) fields.push(`${key}.${key4}`);
              data[Number(key2)-1].push(rp[key][key2].geometry[key4])
            }
          }
        }
        var csv = Papa.unparse({fields:fields,data:data})
        const file = new Blob([csv],{type: 'text/csv;charset=utf=8'});
        
        var fdata = new FormData()
        const toAdd = {
          title: `${this.title}_form`,
          overwrite: true,
          type: 'CSV',
          fileName: `${this.title}_form.csv`,
          f: 'json'
        }
        for (var k in toAdd) {
          fdata.append(k, toAdd[k])
        }
        fdata.append('file',file,`${this.title}_form.csv`);
        this.$store.state.esriModules.esriRequest.then(([esriRequest])=>{
          esriRequest(`${this.$store.state.portal.user.userContentUrl}/addItem`,{
            method: 'post',
            body: fdata 
          }).then((responseCSV)=>{
            var self = this;
            setTimeout(function(){
              self.publishCSV(responseCSV, fields)
            },1000)
          }, (error)=>{
            console.log(error);
            this.$store.set('jobStatus', 'failed')
            this.$store.set('messages', error.details.messages)
          });
        });
      }
    }
  }
}
</script>

<style>
.resize {
  transition: all 0.5s ease;
}

.close{
  position: relative;
  top: 4px;
  right: 4px;
  font-size: 16px;
}
</style>