<template>
  <transition name="fade">
    <div id="AppMap">
      <div id="mainview">
        <b-modal v-model="assign.display" centered :title="assign.route" size="lg">
          <b-form>
            <div class="d-flex flex-wrap justify-content-center">
              <div class="form-group mr-4">
                <label>Assignment Type</label>
                <b-form-select v-model="assign.selectedType" :options="assign.types" class="form-control" />
              </div>
              <div class="form-group mr-4">
                <label>Assign to</label>
                <b-form-select v-model="assign.selectedWorker" :options="assign.workers" class="form-control"/>
              </div>
              <div class="form-group mr-4">
                <label>Priority</label>
                <b-form-select v-model="assign.selectedPriority" class="form-control" >
                  <option value="0">None</option>
                  <option value="1">Low</option>
                  <option value="2">Medium</option>
                  <option value="3">High</option>
                  <option value="4">Critical</option>
                </b-form-select>
              </div>
              <div class="form-group">
                <label>Description</label>
                <textarea class="form-control" rows="3" cols="50" v-model="assign.description"></textarea>
              </div>
            </div>
          </b-form>
          <b-btn slot="modal-footer" block @click="assignRoute">Assign</b-btn>
        </b-modal>
        <UploadWidget ref="uploadWidget" @updateStatus="(data)=>{$emit('updateStatus', data)}"></UploadWidget>
        <div id="viewDiv2"></div>
      </div>
    </div>
  </transition>
</template>

<script>
import UploadWidget from '@/components/UploadWidget'
import {get} from 'vuex-pathify'

export default {
  name: 'appMap',
  components: {
    UploadWidget
  },
  computed: {
    jobId: get('jobId')
  },
  data: function() {
    return {
      jobLayerName: ['Stops','Depots'],
      assign: {
        display: false,
        route: "",
        workers: [],
        types: [],
        selectedWorker: "",
        selectedType: "",
        selectedPriority: "",
        description: "",
        url: ""
      }
    }
  },
  created: function() {
    if (this.jobId) {
      this.loadStops();
      this.loadRoutes();
    }
  },
  watch: {
    //on job id change
    jobId: function() {
      console.log(this.map,this.mapView)
      if (this.jobId) {
        var toRemove = this.map.layers.items.filter((layer)=>{
          return this.jobLayerName.indexOf(layer.title) > -1
        });
        console.log(toRemove)
        this.map.removeMany(toRemove);
        this.jobLayerName = ['Stops','Depots'];
        this.loadRoutes();
        this.loadStops();
      }
    }
  },
  methods: {
    //generate random colour for rendering a route
    getRand: function() {
      return Math.floor((Math.random() * 256));
    },
    //Appends geometry to stops since the server returns stops with no geometry
    addGeometry: function(orders, depots, stops) {
      console.log({orders:orders,depots:depots,stops:stops})
      for (var i = 0; i < stops.value.features.length; i++) {
        for (var j = 0; j < orders.value.features.length; j++) {
          if (stops.value.features[i].attributes.Name == orders.value.features[j].attributes.Name) {stops.value.features[i].geometry = orders.value.features[j].geometry}
        }
        for (var k = 0; k < depots.value.features.length; k ++) {
          if (stops.value.features[i].attributes.Name == depots.value.features[k].attributes.Name) {stops.value.features[i].geometry = depots.value.features[k].geometry}
        }
      }
      return stops;
    },
    //Automatically create a popup template for anything added to the map
    makeTemplate:function(featureLayer,action) {
      let template = {
        title: "{Name}",
        content: [{
          type: "fields",
          fieldInfos: []
        }]
      }
      for (var i = 0, l = featureLayer.fields.length; i<l; i++) {
        if (featureLayer.fields[i].alias === 'GlobalID') continue;
        template.content[0].fieldInfos.push({
          fieldName: featureLayer.fields[i].name,
          label: featureLayer.fields[i].alias,
          visible: true
        })
      }
      if (action === 'assign') {
        let assignAction = {
          title: "Assign Route",
          id: "assignRoute",
          className: "esri-icon-group"
        }
        template.actions = [assignAction];
      } else if (action === 'changeRoute') {
        let assignAction = {
          title: "Change Route",
          id: "changeRoute",
          className: "esri-icon-directions"
        }
        template.actions = [assignAction];
      }
      featureLayer.popupTemplate = template;
      return featureLayer;
    },
    //Load a feature layer from ArcGIS Online
    loadItem: function(elem) {
      this.$store.state.esriModules.FeatureLayer.then(([FeatureLayer])=>{
        var item = new FeatureLayer({
          portalItem: {
            id: elem.id
          },
          refreshInterval: 0.5
        });
        item.load().then(()=>{
          if (item.title.length > 19) item.title = item.title.substring(0,17) + '...';
          item = this.makeTemplate(item);
          this.map.add(item);
        });
      });
    },
    //Load the routes from the server
    loadRoutes: function() {
      console.log('loading routes')
      const modules = this.$store.state.esriModules;
      Promise.all([modules.Geoprocessor,modules.FeatureLayer]).then(([[Geoprocessor],[FeatureLayer]])=>{
        var task = new Geoprocessor(`https://logistics.arcgis.com/arcgis/rest/services/World/VehicleRoutingProblem/GPServer/SolveVehicleRoutingProblem`);
        task.getResultData(this.jobId, 'out_routes').then((stops)=>{
          stops.value.features.forEach((feature)=>{
            let renderer = {
              type: 'simple',
              symbol: {
                type: 'simple-line',
                color: [this.getRand(), this.getRand(), this.getRand()],
                width: 4
              }
            };
            var routes = new FeatureLayer({
              source: [feature],
              objectIdField: 'ObjectID',
              fields: stops.value.fields,
              geometryType: 'polyline',
              renderer: renderer,
              title: feature.attributes.Name
            });
            this.jobLayerName.push(feature.attributes.Name);
            console.log(routes)
            const newRoutes = this.makeTemplate(routes, 'assign');
            console.log(this.map)
            this.map.add(newRoutes,1)
          });
        },(error)=>{
          console.log(error)
        });
      });
    },
    //Load the stops from the server
    loadStops: function() {
      console.log('loading stops')
      const modules = this.$store.state.esriModules;
      Promise.all([modules.Geoprocessor,modules.FeatureLayer]).then(([[Geoprocessor],[FeatureLayer]])=>{
        var task = new Geoprocessor(`https://logistics.arcgis.com/arcgis/rest/services/World/VehicleRoutingProblem/GPServer/SolveVehicleRoutingProblem`)
        var stop_p = task.getResultData(this.jobId, 'out_stops');
        var order_p = task.getResultData(this.jobId, 'orders',{url:`https://logistics.arcgis.com/arcgis/rest/services/World/VehicleRoutingProblem/GPServer/SolveVehicleRoutingProblem/jobs/${this.jobId}/inputs/orders`});

        var depot_p = task.getResultData(this.jobId, 'depots',{url:`https://logistics.arcgis.com/arcgis/rest/services/World/VehicleRoutingProblem/GPServer/SolveVehicleRoutingProblem/jobs/${this.jobId}/inputs/depots`});
        Promise.all([order_p,depot_p,stop_p]).then((lst)=>{
          var stopGeo = this.addGeometry(lst[0],lst[1],lst[2]);
          this.stopGeo = stopGeo;
          let renderer = {
            type: 'unique-value',
            field: 'StopType',
            defaultSymbol: {
              type: 'simple-marker',
              color: [40, 240, 40],
              size: '8px'
            },
            uniqueValueInfos: [{
              value: '0',
              symbol: {
                type: 'simple-marker',
                color: [240, 20, 20],
                size: '8px'
              }
            }]
          };

          var stops = new FeatureLayer({
            source: stopGeo.value.features,
            objectIdField: 'ObjectID',
            fields: stopGeo.value.fields,
            geometryType: 'point',
            renderer: renderer,
            title: 'Stops'
          });
          const newStops = this.makeTemplate(stops, 'changeRoute');
          console.log(newStops);
          this.map.add(newStops);
          stops.when(()=>{
            stops.queryExtent().then((response)=>{
              this.mapView.when(function() {
                this.mapView.goTo(response.extent.expand(2.0));
              });
            });
          });
        });
      });
    },
    //Assign the selected route to Workforce
    assignRoute: function() {
      this.assign.display = false;
      const modules = this.$store.state.esriModules;
      Promise.all([modules.FeatureLayer,modules.esriRequest,modules.Point,modules.Projection]).then(([[FeatureLayer],[esriRequest],[Point],[Projection]])=>{

        //filter features that are on selected route
        var assignArr = this.stopGeo.value.features.filter((elem)=>{
          return (elem.attributes.RouteName === this.assign.route && elem.attributes.StopType === 0) 
        });

        //sort in order of arrival time
        assignArr.sort((a,b)=>{
          return a.attributes.Sequence - b.attributes.Sequence
        });

        //search portal for a dispather feature layer
        this.portal.queryItems({
          query: 'title:dispatchers_ AND access:shared AND type:Feature Service'
        }).then((result)=>{

          var dispatchers = new FeatureLayer({
            portalItem: result.results[0]
          });

          dispatchers.load().then(()=>{
            var dispatcherQuery = dispatchers.createQuery();
            dispatcherQuery.outFields = ['OBJECTID'];
            dispatcherQuery.where = `userId = '${this.portal.user.username}'`;
            dispatchers.queryFeatures(dispatcherQuery).then((result2)=>{
              Projection.load().then(()=>{

                //on dispatcher load, create the json for sending to rest api
                var features = assignArr.map((elem)=>{
                  let point = new Point({
                    latitude: elem.geometry.y,
                    longitude: elem.geometry.x,
                    spatialReference: {wkid: 4326}
                  });
                  let projected = Projection.project(point, {wkid: 102100});
                  return {
                    geometry: {
                      x: projected.x,
                      y: projected.y
                    },
                    attributes: {
                      status: 1,
                      assignmentType: Number(this.assign.selectedType),
                      location: elem.attributes.Name,
                      assignmentRead: 0,
                      dispatcherId: result2.features[0].attributes.OBJECTID,
                      description: this.assign.description,
                      priority: Number(this.assign.selectedPriority),
                      workerId: Number(this.assign.selectedWorker),
                      assignedDate: new Date().getTime()
                    }
                  };
                });

                //send json to rest api
                esriRequest(this.assign.url, {
                  method: 'post',
                  query: {
                    f: 'json',
                    features: JSON.stringify(features)
                  }
                }).then((response)=>{
                  this.$emit('updateStatus',{status:'success'});
                }, (error)=>{
                  console.log(error);
                  console.log(error.details.messages);
                  this.$emit('updateStatus', {status: 'failed', messages: error.details.messages})
                });
              });
            });
          });
        });
      });
    }
  },
  //Initialize the map on mount
  mounted: function() {
    const modules = this.$store.state.esriModules
    Promise.all([modules.Map,modules.MapView,modules.LayerList,modules.Portal,modules.FeatureLayer,modules.MapImageLayer]).then(([[Map],[MapView],[LayerList],[Portal],[FeatureLayer],[MapImageLayer]])=>{
      this.map = new Map({
        basemap: "streets-navigation-vector"
      });

      this.mapView = new MapView({
        map: this.map,
        container: 'viewDiv2',
        center: [-79.3854,43.64869],
        zoom: 10
      });

      
      var layerList = new LayerList({
        view: this.mapView,
        listItemCreatedFunction: (event)=>{
          var item = event.item;
          if ((!(this.jobLayerName.indexOf(item.title) > -1))&& item.title != 'Live Traffic') {
            item.actionsSections = [[{
              title: 'Remove',
              className: 'esri-icon-trash',
              id: 'remove'
            }]];
          }
        }
      });

      layerList.on('trigger-action', (event)=>{
        if (event.action.id === 'remove') this.map.remove(event.item.layer)
      });

      this.mapView.ui.add(layerList, {
        position: 'top-right'
      });

      this.mapView.ui.add(this.$refs.uploadWidget.$el, "top-left");
      
      this.mapView.popup.on('trigger-action',(event)=>{
        if (event.action.id === 'assignRoute') {
          this.assign.display = true;
          this.assign.route = event.target.title;
        }
      })
      
      this.portal = new Portal({
        authMode: 'immediate'
      });

      this.portal.load().then(()=>{
        var traffic = new MapImageLayer({
          portalItem: {
            id: 'ff11eb5b930b4fabba15c47feb130de4'
          },
          visible: false,
          title: 'Live Traffic'
        });
        this.map.add(traffic,0);

        //Populate list of workers
        this.portal.queryItems({
          query: 'title:workers_ AND access:shared AND type:Feature Service'
        }).then((queryResult)=>{
          var workers = new FeatureLayer({
            portalItem: queryResult.results[0]
          });
          var workerQuery = workers.createQuery();
          workerQuery.outFields = ["Name", "OBJECTID"];
          workers.queryFeatures(workerQuery).then((result)=>{
            result.features.forEach((feature)=>{
              this.assign.workers.push({value: feature.attributes.OBJECTID, text: feature.attributes.name})
            });
          });
        });

        //Populate list of assignment types
        this.portal.queryItems({
          query: 'title:assignments_ AND access:shared AND type:Feature Service'
        }).then((queryResult)=>{
          this.assign.url = queryResult.results[0].url + '/0/addFeatures';
          var assignments = new FeatureLayer({
            portalItem: queryResult.results[0]
          });
          assignments.load();
          assignments.when(()=>{
            var assignTypeField = assignments.fields.find((elem)=>{
              return (elem.name === "assignmentType");
            });
            assignTypeField.domain.codedValues.forEach((elem)=>{
              this.assign.types.push({value: elem.code, text: elem.name});
            });
          });
        });
      });
    });
  }
}
</script>
<style>
#viewDiv2 {
  width: 100% !important;
  height: 100% !important;
  z-index: -1;
  top: 0;
  bottom: 0;

}

#AppMap {
  height: calc(100vh - 56px);
}

#mainview {
  height: 100%;
  position: relative;
}



.esri-layer-list {
  width: auto !important;
}




</style>